@import "sass/index";

.wrapper {
  padding-top: 0;
}

.tableWrapper {
  padding-top: spacing(4);
}

.coinWrapper {
}

.coinIcon {
  position: static;
  height: 16px;
  width: 16px;
  cursor: default;
